/* ==========================================================================
   Author Info widget style - begin
   ========================================================================== */

aside.mkdf-sidebar{
    .mkdf-author-info-widget{
        padding: 40px 7.5%;
    }
}
.widget {
    &.mkdf-author-info-widget {
        border: 1px solid $default-text-color;
        text-align: center;
        .mkdf-aiw-title{
            margin-bottom: 10px;
            letter-spacing: 0;
        }
        .mkdf-aiw-inner {
            ul {
                &.mkdf-aiw-social-networks {
                    margin-top: 20px;
                    li {
                        display: inline-block;
                        margin: 0 18px 0 0;
                        padding: 0;
                        &:last-child {
                            margin-right: 0;
                        }
                    }
                    a {
                        font-size: 13px;
                        color: #000;
                        &:hover{
                            color: $default-text-color;
                        }
                    }
                }
            }
        }
        .mkdf-aiw-image {
            display: inline-block;
            margin-bottom: 2px;
            img {
                border-radius: 100%;
            }
        }
        .mkdf-aiw-text {
            p{
                margin: 0;
            }
        }
    }
}
/* ==========================================================================
   Author Info widget style - end
   ========================================================================== */